import * as THREE from 'three'
import { shaderMaterial } from '@react-three/drei'
import { extend } from '@react-three/fiber'

// BlobMaterial 
const BlobMaterial = shaderMaterial(
  {
    time: 0,
    color1: new THREE.Color('#fff'), // 
    color2: new THREE.Color('#fff'), // 
    specularColor: new THREE.Color('#ffffff'), //    ()
    shininess: 50, //   ( )
    waveIntensity: 0.3, //   
  },
  `
// Vertex Shader
  varying vec2 vUv;
  varying vec3 vNormal; //  
  uniform float time;
  uniform float waveIntensity; //  
  void main() {
    vUv = uv;
    vNormal = normalize(normal);
    vec3 pos = position;
    float wave = sin(pos.x * 15.0 + time * 2.0) * waveIntensity;
    pos.z += wave;
    gl_Position = projectionMatrix * modelViewMatrix * vec4(pos, 1.0);
  }
`,
  `
// Fragment Shader
  varying vec2 vUv;
  varying vec3 vNormal;
  uniform vec3 color1;
  uniform vec3 color2;
  uniform vec3 specularColor;
  uniform float shininess;

  void main() {
    float u = vUv.x;
    float v = vUv.y;

    //     
    vec3 finalColor = mix(color1, color2, smoothstep(0.1, 0.6, v));

    //   
    vec3 lightDir = normalize(vec3(0.5, 0.8, 2.5)); //   
    vec3 viewDir = normalize(-gl_FragCoord.xyz); //  

    //   
    vec3 reflectDir = reflect(viewDir, vNormal);
    float spec = pow(max(dot(reflectDir, lightDir), 0.3), shininess);

    //    
    vec3 ambient = vec3(0.0); // 
    vec3 diffuse = max(dot(vNormal, lightDir), 0.2) * finalColor;
    vec3 specular = specularColor * spec;

    gl_FragColor = vec4(ambient + diffuse + specular, 1.0);
  }
`
)

extend({ BlobMaterial })

export default BlobMaterial
